import React, { useState, useEffect } from 'react';
import { useAuth } from '../context/AuthContext';
import { 
    getUserNegotiations, 
    makeCounterOffer, 
    acceptOffer, 
    declineOffer, 
    cancelNegotiation 
} from '../services/negotiationService';

const NegotiationsDashboard = () => {
    const { user } = useAuth();
    const [negotiations, setNegotiations] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [activeTab, setActiveTab] = useState('all');
    const [typeFilter, setTypeFilter] = useState('all');
    const [selectedNegotiation, setSelectedNegotiation] = useState(null);
    const [counterOffer, setCounterOffer] = useState('');
    const [counterMessage, setCounterMessage] = useState('');
    const [actionLoading, setActionLoading] = useState(false);

    useEffect(() => {
        if (user) {
            fetchNegotiations();
        }
    }, [user, activeTab, typeFilter]);

    const fetchNegotiations = async () => {
        try {
            setLoading(true);
            const response = await getUserNegotiations(activeTab, typeFilter);
            setNegotiations(response.negotiations);
        } catch (error) {
            setError(error.message);
        } finally {
            setLoading(false);
        }
    };

    const handleCounterOffer = async (negotiationId) => {
        if (!counterOffer || parseFloat(counterOffer) <= 0) {
            setError('Please enter a valid counter offer amount');
            return;
        }

        try {
            setActionLoading(true);
            await makeCounterOffer(
                negotiationId, 
                parseFloat(counterOffer), 
                counterMessage || `Counter offer of $${counterOffer}`
            );
            
            setCounterOffer('');
            setCounterMessage('');
            setSelectedNegotiation(null);
            fetchNegotiations();
        } catch (error) {
            setError(error.message);
        } finally {
            setActionLoading(false);
        }
    };

    const handleAcceptOffer = async (negotiationId) => {
        if (!window.confirm('Are you sure you want to accept this offer? This will complete the negotiation.')) {
            return;
        }

        try {
            setActionLoading(true);
            await acceptOffer(negotiationId);
            fetchNegotiations();
        } catch (error) {
            setError(error.message);
        } finally {
            setActionLoading(false);
        }
    };

    const handleDeclineOffer = async (negotiationId, reason = '') => {
        try {
            setActionLoading(true);
            await declineOffer(negotiationId, reason);
            fetchNegotiations();
        } catch (error) {
            setError(error.message);
        } finally {
            setActionLoading(false);
        }
    };

    const handleCancelNegotiation = async (negotiationId, reason = '') => {
        if (!window.confirm('Are you sure you want to cancel this negotiation?')) {
            return;
        }

        try {
            setActionLoading(true);
            await cancelNegotiation(negotiationId, reason);
            fetchNegotiations();
        } catch (error) {
            setError(error.message);
        } finally {
            setActionLoading(false);
        }
    };

    const getLatestOffer = (negotiation) => {
        return negotiation.offers
            .filter(offer => offer.status === 'pending')
            .sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp))[0];
    };

    const canTakeAction = (negotiation) => {
        const latestOffer = getLatestOffer(negotiation);
        return latestOffer && latestOffer.toUser._id === user.id;
    };

    const getStatusBadgeClass = (status) => {
        switch (status) {
            case 'active': return 'bg-primary';
            case 'completed': return 'bg-success';
            case 'cancelled': return 'bg-secondary';
            case 'expired': return 'bg-warning';
            default: return 'bg-secondary';
        }
    };

    const formatDate = (date) => {
        return new Date(date).toLocaleDateString('en-US', {
            year: 'numeric',
            month: 'short',
            day: 'numeric',
            hour: '2-digit',
            minute: '2-digit'
        });
    };

    if (!user) {
        return (
            <div className="container mt-5">
                <div className="alert alert-warning">
                    Please log in to view your negotiations.
                </div>
            </div>
        );
    }

    return (
        <div className="container mt-4">
            <h2 className="mb-4">
                <i className="fas fa-handshake me-2"></i>
                My Negotiations
            </h2>

            {error && (
                <div className="alert alert-danger">
                    {error}
                    <button className="btn-close float-end" onClick={() => setError('')}></button>
                </div>
            )}

            {/* Filter Tabs */}
            <div className="row mb-4">
                <div className="col-md-8">
                    <ul className="nav nav-pills">
                        <li className="nav-item">
                            <button 
                                className={`nav-link ${activeTab === 'all' ? 'active' : ''}`}
                                onClick={() => setActiveTab('all')}
                            >
                                All
                            </button>
                        </li>
                        <li className="nav-item">
                            <button 
                                className={`nav-link ${activeTab === 'active' ? 'active' : ''}`}
                                onClick={() => setActiveTab('active')}
                            >
                                Active
                            </button>
                        </li>
                        <li className="nav-item">
                            <button 
                                className={`nav-link ${activeTab === 'completed' ? 'active' : ''}`}
                                onClick={() => setActiveTab('completed')}
                            >
                                Completed
                            </button>
                        </li>
                        <li className="nav-item">
                            <button 
                                className={`nav-link ${activeTab === 'cancelled' ? 'active' : ''}`}
                                onClick={() => setActiveTab('cancelled')}
                            >
                                Cancelled
                            </button>
                        </li>
                    </ul>
                </div>
                <div className="col-md-4">
                    <select 
                        className="form-select" 
                        value={typeFilter} 
                        onChange={(e) => setTypeFilter(e.target.value)}
                    >
                        <option value="all">All Negotiations</option>
                        <option value="client">As Client</option>
                        <option value="provider">As Provider</option>
                    </select>
                </div>
            </div>

            {loading ? (
                <div className="text-center py-4">
                    <div className="spinner-border" role="status">
                        <span className="visually-hidden">Loading...</span>
                    </div>
                </div>
            ) : negotiations.length === 0 ? (
                <div className="alert alert-info">
                    <i className="fas fa-info-circle me-2"></i>
                    No negotiations found.
                </div>
            ) : (
                <div className="row">
                    {negotiations.map((negotiation) => {
                        const isClient = negotiation.client._id === user.id;
                        const latestOffer = getLatestOffer(negotiation);
                        const canAct = canTakeAction(negotiation);

                        return (
                            <div key={negotiation._id} className="col-md-6 mb-4">
                                <div className="card h-100">
                                    <div className="card-header d-flex justify-content-between">
                                        <h6 className="mb-0">
                                            {negotiation.service.title}
                                        </h6>
                                        <span className={`badge ${getStatusBadgeClass(negotiation.status)}`}>
                                            {negotiation.status.charAt(0).toUpperCase() + negotiation.status.slice(1)}
                                        </span>
                                    </div>
                                    <div className="card-body">
                                        <div className="mb-2">
                                            <small className="text-muted">
                                                {isClient ? 'Provider: ' : 'Client: '}
                                                <strong>
                                                    {isClient ? negotiation.provider.name : negotiation.client.name}
                                                </strong>
                                            </small>
                                        </div>
                                        
                                        <div className="price-info mb-3">
                                            <div className="row">
                                                <div className="col-6">
                                                    <small className="text-muted">Base Price:</small>
                                                    <div className="fw-bold">${negotiation.basePrice}</div>
                                                </div>
                                                <div className="col-6">
                                                    <small className="text-muted">Current Offer:</small>
                                                    <div className="fw-bold text-primary">
                                                        ${negotiation.currentOffer}
                                                    </div>
                                                </div>
                                            </div>
                                            {negotiation.finalPrice && (
                                                <div className="mt-2">
                                                    <small className="text-muted">Final Price:</small>
                                                    <div className="fw-bold text-success">
                                                        ${negotiation.finalPrice}
                                                    </div>
                                                </div>
                                            )}
                                        </div>

                                        {latestOffer && (
                                            <div className="latest-offer mb-3">
                                                <small className="text-muted">Latest Offer:</small>
                                                <div className="border-start border-3 border-primary ps-2">
                                                    <div className="fw-bold">
                                                        ${latestOffer.offeredPrice} by {latestOffer.fromUser.name}
                                                    </div>
                                                    {latestOffer.message && (
                                                        <small className="text-muted">
                                                            "{latestOffer.message}"
                                                        </small>
                                                    )}
                                                </div>
                                            </div>
                                        )}

                                        <small className="text-muted">
                                            Created: {formatDate(negotiation.createdAt)}
                                        </small>

                                        {negotiation.status === 'active' && (
                                            <div className="mt-3">
                                                {canAct && latestOffer ? (
                                                    <div className="d-flex gap-2 flex-wrap">
                                                        <button 
                                                            className="btn btn-success btn-sm"
                                                            onClick={() => handleAcceptOffer(negotiation._id)}
                                                            disabled={actionLoading}
                                                        >
                                                            <i className="fas fa-check me-1"></i>
                                                            Accept
                                                        </button>
                                                        <button 
                                                            className="btn btn-warning btn-sm"
                                                            onClick={() => setSelectedNegotiation(negotiation._id)}
                                                            disabled={actionLoading}
                                                        >
                                                            <i className="fas fa-reply me-1"></i>
                                                            Counter
                                                        </button>
                                                        <button 
                                                            className="btn btn-outline-danger btn-sm"
                                                            onClick={() => handleDeclineOffer(negotiation._id)}
                                                            disabled={actionLoading}
                                                        >
                                                            <i className="fas fa-times me-1"></i>
                                                            Decline
                                                        </button>
                                                    </div>
                                                ) : (
                                                    <div className="d-flex gap-2">
                                                        <button 
                                                            className="btn btn-outline-warning btn-sm"
                                                            onClick={() => setSelectedNegotiation(negotiation._id)}
                                                            disabled={actionLoading}
                                                        >
                                                            <i className="fas fa-reply me-1"></i>
                                                            Make Offer
                                                        </button>
                                                        <button 
                                                            className="btn btn-outline-secondary btn-sm"
                                                            onClick={() => handleCancelNegotiation(negotiation._id)}
                                                            disabled={actionLoading}
                                                        >
                                                            <i className="fas fa-ban me-1"></i>
                                                            Cancel
                                                        </button>
                                                    </div>
                                                )}
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            )}

            {/* Counter Offer Modal */}
            {selectedNegotiation && (
                <div className="modal fade show" style={{ display: 'block' }} tabIndex="-1">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">Make Counter Offer</h5>
                                <button 
                                    type="button" 
                                    className="btn-close" 
                                    onClick={() => setSelectedNegotiation(null)}
                                ></button>
                            </div>
                            <div className="modal-body">
                                <div className="mb-3">
                                    <label className="form-label">Counter Offer Amount</label>
                                    <div className="input-group">
                                        <span className="input-group-text">$</span>
                                        <input
                                            type="number"
                                            className="form-control"
                                            value={counterOffer}
                                            onChange={(e) => setCounterOffer(e.target.value)}
                                            placeholder="Enter amount"
                                            min="0"
                                            step="0.01"
                                        />
                                    </div>
                                </div>
                                <div className="mb-3">
                                    <label className="form-label">Message (Optional)</label>
                                    <textarea
                                        className="form-control"
                                        rows="3"
                                        value={counterMessage}
                                        onChange={(e) => setCounterMessage(e.target.value)}
                                        placeholder="Explain your counter offer..."
                                    />
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button 
                                    type="button" 
                                    className="btn btn-secondary" 
                                    onClick={() => setSelectedNegotiation(null)}
                                    disabled={actionLoading}
                                >
                                    Cancel
                                </button>
                                <button 
                                    type="button" 
                                    className="btn btn-primary"
                                    onClick={() => handleCounterOffer(selectedNegotiation)}
                                    disabled={actionLoading || !counterOffer}
                                >
                                    {actionLoading ? (
                                        <>
                                            <i className="fas fa-spinner fa-spin me-1"></i>
                                            Sending...
                                        </>
                                    ) : (
                                        'Send Counter Offer'
                                    )}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
            {selectedNegotiation && <div className="modal-backdrop fade show"></div>}
        </div>
    );
};

export default NegotiationsDashboard;
